import React from 'react';

import { Helmet } from 'react-helmet';
import { Accordion, Card, Button } from 'react-bootstrap';

function Faq() {
  return (
    <div>
      <Helmet>
          <title>Faq - KlayHosting</title>
      </Helmet>

      <div className="pt-5 pb-2 contentContainer text-center">
          <span className="h1 text-white">Faq</span><br />
          <span className="text-muted">Frequently Asked Questions</span>
      </div>

      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 101"><path fill="#1A1A1A" fillOpacity="1" d="M0,100L1440,0L1440,0L0,0Z"></path></svg>

      <div className="mx-auto w-75 mt-5 mb-5">
        <Accordion defaultActiveKey="0">
          <Card>
            <Card.Header>
              <Accordion.Toggle as={Button} variant="link" eventKey="0">
                How can I create a Bot?  
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey="0">
              <Card.Body>
                Creating a Bot has never been easier! <br />
                <br />
                Below we will list the various steps to follow in order to activate your Bot: <br />
                1) Go to <a href="https://billing.klayhosting.com/register" target="_blank" rel="noreferrer">https://billing.klayhosting.com/register</a> and create an account <br />
                2) Log in with the credentials chosen in the previous step at <a href="https://billing.klayhosting.com/login" target="_blank" rel="noreferrer">https://billing.klayhosting.com/login</a> <br />
                3) Immediately after logging in, request verification by email of the account via the yellow alert box that will appear at the top of the shop <br />
                4) (optional) If you wish to further verify your account, go to your profile page by pressing on your name at the top right and link the discord account to your shop account <br />
                5) To activate a plan, go to the "<a href="https://billing.klayhosting.com/servers" target="_blank" rel="noreferrer">Servers</a>" section and click on "Create Server" at the top right <br />
                6) Enter all the necessary information and in the last option "Resource Configuration" select the type of plan you want to use for your bot <br />
                7) To finalize the purchase press "Submit" <br />
                8) In order to manage your bot (Console, File Manager, Access, etc ...) press the "Manage" button of your Bot in the "<a href="https://billing.klayhosting.com/servers" target="_blank" rel="noreferrer">Servers</a>" section <br />
                9) Immediately after you will be redirected to the <a href="https://panel.klayhosting.com" target="_blank" rel="noreferrer">Management Panel</a> of your bots, log in with the same credentials used in the shop and start managing your fantastic Bot! 
              </Card.Body>
            </Accordion.Collapse>
          </Card>

          <Card>
            <Card.Header>
              <Accordion.Toggle as={Button} variant="link" eventKey="1">
                What Programming Languages do you support?
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey="1">
              <Card.Body>
                We currently support Bots written in <a href="https://discord.js.org" target="_blank" rel="noreferrer">Javascript</a>, <a href="https://discordpy.readthedocs.io/en/stable" target="_blank" rel="noreferrer">Python</a>, <a href="https://github.com/DV8FromTheWorld/JDA" target="_blank" rel="noreferrer">Java</a> and <a  href="https://goscord.dev" target="_blank" rel="noreferrer">Golang</a>.
              </Card.Body>
            </Accordion.Collapse>
          </Card>

          <Card>
            <Card.Header>
              <Accordion.Toggle as={Button} variant="link" eventKey="2">
                How long will it take to be able to use the Bot after redeeming it?
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey="2">
              <Card.Body>
                After redeeming the bot, in order to manage it you will need to go to the panel at <a href="https://panel.klayhosting.com" target="_blank" rel="noreferrer">https://panel.klayhosting.com</a><br />
                Then logging in to the management panel with the same credentials with which you registered on the shop page, in a few seconds the bot will be up and running at 100%!
              </Card.Body>
            </Accordion.Collapse>
          </Card>

          <Card>
            <Card.Header>
              <Accordion.Toggle as={Button} variant="link" eventKey="3">
                How can i create a Database?
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey="3">
              <Card.Body>
                Here are the steps to follow: <br />
                1) Go to the bot management panel at <a href="https://panel.klayhosting.com/server" target="_blank" rel="noreferrer">https://panel.klayhosting.com/server</a> <br />
                2) Select your bot <br />
                3) Go to the "Database" sub-category <br />
                4) Press in "New Database" and enter the name of the database you prefer in the first available field <br />
                5) (optional) If you want to limit access to your database to only certain ip you can also change the "Connections From" field below <br />
                6) Confirm the creation of the database by clicking on the "Create Database" button <br />
                7) Copy the "Database Name" (which is probably slightly different from the one you chose in step "4" to avoid overlapping) on ​​the left. Then press in the eye and also copy "Username" and "Password" <br />
                8) Go to the database management page at <a href="https://mysql.klayhosting.com" target="_blank" rel="noreferrer">https://mysql.klayhosting.com</a> <br />
                9) Log in with the previously copied credentials and start managing your database!
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          <Card>
            <Card.Header>
              <Accordion.Toggle as={Button} variant="link" eventKey="4">
                Why does my bot constantly stay in the "Starting" state? 
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey="4">
              <Card.Body>
                To make the panel understand that your bot is online, add a console log in your code that prints this message: "Bot Online" <br />
                <br />
                Exemple in Javascript: <br />
                ... <br />
                client.login('bot token here'); <br />
                console.log("Bot Online"); <br />
                ...
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          <Card>
            <Card.Header>
              <Accordion.Toggle as={Button} variant="link" eventKey="5">
                Why do you offer free services?
              </Accordion.Toggle>
            </Card.Header>
            <Accordion.Collapse eventKey="5">
              <Card.Body>
                We provide a completely free non-profit service. <br />
                Our website does not have any form of income. <br />
                Donations are free of charge and help us to keep the servers online in order to guarantee the correct functionality of the service.
              </Card.Body>
            </Accordion.Collapse>
          </Card>
        </Accordion>
      </div>
    </div>
  )
}

export default Faq;